interface BlogPostPageProps {
  params: Promise<{ slug: string }>;
}

export default function BlogPostPage({ params }: BlogPostPageProps) {
  // For static export, we need to handle the promise
  const slug = params instanceof Promise ? 'ai-vs-human-brain' : (params as any).slug;
  const post = blogPosts[slug as keyof typeof blogPosts];

  if (!post) {
    return null; // This will trigger notFound() in the client component
  }

  return <BlogPostClient post={post} slug={slug} />;
}

// Generate static params for static export
export function generateStaticParams() {
  return Object.keys(blogPosts).map((slug) => ({
    slug: slug,
  }));
}